import React from "react";
import QuoteForm from "./AddQuoteForm";
import { Navigation } from "../../lib";
import { connect } from "react-redux";
import { bindActionCreators } from "redux";
import * as missingQuotesActions from "../../actions/missingQuotesActions";

class QuoteFormPage extends React.Component {
  closeQuoteForm() {
    this.props.missingQuotesActions.removeQuoteFormAmount();
    Navigation.go(
      `/quote-form/1/${this.props.match.params.quote_id}/${encodeURIComponent(
        this.props.match.params.previousPage
      )}`
    );
  }

  render() {
    return (
      <div>
        <div className="quote">
          <QuoteForm
            quote_id={this.props.match.params.quote_id}
            previousPage={this.props.match.params.previousPage}
            closeQuoteForm={this.closeQuoteForm.bind(this)}
            authentication={JSON.parse(localStorage.getItem("data"))}
          />
        </div>
      </div>
    );
  }
}
function mapStateToProps(state) {
  return {
    missingQuotes: state.missingQuotes,
  };
}

function mapDispatchToProps(dispatch) {
  return {
    missingQuotesActions: bindActionCreators(missingQuotesActions, dispatch),
  };
}

export default connect(mapStateToProps, mapDispatchToProps)(QuoteFormPage);
