import React from "react";
import { connect } from "react-redux";
import { bindActionCreators } from "redux";
import * as navigationTitleActions from "../../actions/navigationTitleActions";
import * as missingQuotesActions from "../../actions/missingQuotesActions";
import moment from "moment";
import Row from "muicss/lib/react/row";
import Col from "muicss/lib/react/col";
import Panel from "muicss/lib/react/panel";
import Input from "muicss/lib/react/input";
import Select from "muicss/lib/react/select";
import Option from "muicss/lib/react/option";
import Button from "muicss/lib/react/button";
import Form from "muicss/lib/react/form";
import QuoteNotes from "./QuoteFormNotes";
import PercentageInput from "../common/PercentageInput";
import CurrencyInput from "../common/CurrencyInput";

class QuotePage extends React.Component {
  constructor(props) {
    super(props);
    this.state = {
      bank_name: "",
      counter: 0,
      banker: "",
      req_datetime: "",
      deal_name: "",
      quoted_amount: null,
      rate_type: "Fixed",
      spread: null,
      quote_index: "",
      index_today_rate: null,
      rate: null,
      term: null,
      amortization: null,
      ltv: null,
      prepay_type: "Step Down",
      recourse: "",
      number_of_option: 0,
      option: "",
      bank_fee: null,
      prepay_yrs: [],
      quote_notes: [],
      new_quote_notes: [],
      index: "PRIME",
      quote_id: parseInt(this.props.quote_id, 10),
      term_sheet: "Not Selected",
      email_flag: 0,
      is_new: true,
      cancelButtonShowing: true,
    };
    this.rateTypeChange = this.rateTypeChange.bind(this);
    this.quotedAmountChange = this.quotedAmountChange.bind(this);
    this.prepayYearsChange = this.prepayYearsChange.bind(this);
    this.spreadChange = this.spreadChange.bind(this);
    this.changeIndex = this.changeIndex.bind(this);
    this.rateChange = this.rateChange.bind(this);
    this.termChange = this.termChange.bind(this);
    this.termBlur = this.termBlur.bind(this);
    this.amortizationChange = this.amortizationChange.bind(this);
    this.ltvChange = this.ltvChange.bind(this);
    this.prepayChange = this.prepayChange.bind(this);
    this.recourseChange = this.recourseChange.bind(this);
    this.bankFeeChange = this.bankFeeChange.bind(this);
    this.updatePrepaymentPenalty = this.updatePrepaymentPenalty.bind(this);
    this.termSheetChange = this.termSheetChange.bind(this);
  }

  UNSAFE_componentWillMount() {
    this.props.missingQuotesActions.getQuoteDetails(this.props.quote_id);
    this.props.missingQuotesActions.loadRateSpread();
  }

  UNSAFE_componentWillReceiveProps(nextProps) {
    if (
      this.state.bank_name !== nextProps.missingQuotes.editQuoteDetail.bank_name
    ) {
      var quote = nextProps.missingQuotes.editQuoteDetail;
      this.setState({
        bank_name: quote.bank_name ? quote.bank_name : "",
        banker: quote.banker ? quote.banker : "",
        req_datetime: quote.req_datetime ? quote.req_datetime : "",
        deal_name: quote.deal_name ? quote.deal_name : "",
      });
      if (
        this.state.index &&
        this.props.missingQuotes &&
        this.props.missingQuotes.rateSpreadInt
      ) {
        let index_today_rate = this.props.missingQuotes.rateSpreadInt[
          this.state.index.toUpperCase()
        ];
        this.setState({
          index_today_rate: index_today_rate
            ? parseFloat(index_today_rate) / 100
            : null,
        });
      }
    }
  }

  rateTypeChange(e) {
    this.setState({ rate_type: e.target.value });
    if (e.target.value === "Fixed") {
      this.setState({
        quote_index: "",
      });
    } else {
      if (!this.state.spread_type) {
        this.setState({ spread_type: "fix" });
      }
    }
  }

  spreadTypeChange(e) {
    this.setState({ spread_type: e.target.value });
  }

  spreadChange(e) {
    this.setState({ spread: e ? parseFloat(e) / 100 : null });
  }

  spreadBlur(e) {
    let spread = e ? parseFloat(e) / 100 : null;
    let rate =
      (spread || 0) +
      (this.state.manual_index_rate || this.state.index_today_rate);
    this.setState({ rate, spread });
  }

  quotedAmountChange(e) {
    this.setState({ quoted_amount: e });
  }

  prepayYearsChange(index, e) {
    let prepay_yrs = [
      ...this.state.prepay_yrs.slice(0, index),
      e.target.value,
      ...this.state.prepay_yrs.slice(index + 1),
    ];
    this.setState({ prepay_yrs: prepay_yrs });
  }

  changeIndex(e) {
    let index_today_rate = this.props.missingQuotes.rateSpreadInt[
      e.target.value.toUpperCase()
    ];
    index_today_rate = index_today_rate
      ? parseFloat(index_today_rate) / 100
      : null;
    this.setState({
      quote_index: e.target.value.toUpperCase(),
      index: e.target.value,
      index_today_rate,
      manual_index_rate: null,
    });
    var rate =
      parseFloat(this.state.spread ? this.state.spread : 0) + index_today_rate;
    this.setState({ rate });
  }

  indexRateChange(e) {
    this.setState({ manual_index_rate: e ? parseFloat(e) / 100 : null });
  }

  rateChange(e) {
    this.setState({ rate: e ? parseFloat(e) / 100 : null });
  }

  rateFloorChange(e) {
    this.setState({ rate_floor: e });
  }

  termChange(e) {
    this.setState({ term: e.target.value.replace(/\D/g, "") });
  }

  termBlur(e) {
    var termInt = parseInt(e.target.value, 10);

    if (termInt > 15) {
      termInt = 15;
    }

    if (termInt < this.state.prepay_yrs.length) {
      this.setState(
        {
          prepay_yrs: this.state.prepay_yrs.slice(
            0,
            parseInt(e.target.value, 10)
          ),
        },
        function () {
          this.updatePrepaymentPenalty();
          this.setState({ prepay_yrs: this.state.prepay_yrs });
        }
      );
    } else {
      var diff = termInt - this.state.prepay_yrs.length;
      for (var i = 0; i < diff; i++) {
        this.state.prepay_yrs.push(" ");
      }
      this.updatePrepaymentPenalty();
      this.setState({ prepay_yrs: this.state.prepay_yrs });
    }
  }

  updatePrepaymentPenalty() {
    if (this.state.prepay_type === "Step Down") {
      for (var a = 0; a < this.state.prepay_yrs.length; a++) {
        let prepay_yrs = this.state.prepay_yrs;
        prepay_yrs[a] = parseInt(this.state.term, 10) - a;
        this.setState({ prepay_yrs: prepay_yrs });
      }
    } else if (this.state.prepay_type === "Yield Maintenance") {
      for (var b = 0; b < this.state.prepay_yrs.length; b++) {
        let prepay_yrs = this.state.prepay_yrs;
        prepay_yrs[b] = "YM";
        this.setState({ prepay_yrs: prepay_yrs });
      }
    } else if (this.state.prepay_type === "Defeasance") {
      for (var c = 0; c < this.state.prepay_yrs.length; c++) {
        let prepay_yrs = this.state.prepay_yrs;
        prepay_yrs[c] = "DF";
        this.setState({ prepay_yrs: prepay_yrs });
      }
    } else if (this.state.prepay_type === "Net Loss") {
      for (var d = 0; d < this.state.prepay_yrs.length; d++) {
        let prepay_yrs = this.state.prepay_yrs;
        prepay_yrs[d] = "NL";
        this.setState({ prepay_yrs: prepay_yrs });
      }
    } else if (this.state.prepay_type === "Swab Breakage") {
      for (var e = 0; e < this.state.prepay_yrs.length; e++) {
        let prepay_yrs = this.state.prepay_yrs;
        prepay_yrs[e] = "SB";
        this.setState({ prepay_yrs: prepay_yrs });
      }
    } else if (this.state.prepay_type === "No Prepay Penalty") {
      for (var f = 0; f < this.state.prepay_yrs.length; f++) {
        let prepay_yrs = this.state.prepay_yrs;
        prepay_yrs[f] = 0;
        this.setState({ prepay_yrs: prepay_yrs });
      }
    }
  }

  amortizationChange(e) {
    this.setState({ amortization: e.target.value.replace(/\D/g, "") });
  }

  ltvChange(e) {
    this.setState({ ltv: e.target.value.replace(/\D/g, "") });
  }

  prepayChange(e) {
    this.setState({ prepay_type: e.target.value }, function () {
      this.updatePrepaymentPenalty();
    });
  }

  recourseChange(e) {
    this.setState({ recourse: e.target.value });
  }

  numberOfOptions(e) {
    this.setState({ number_of_option: e.target.value.replace(/[^0-9]/g, "") });
  }

  yearsPerOption(e) {
    this.setState({ option: e.target.value });
  }

  bankFeeChange(e) {
    this.setState({ bank_fee: e.target.value.replace(/[^0-9.]/g, "") });
  }

  termSheetChange(e) {
    this.setState({ term_sheet: e.target.value });
  }

  submitForm(e) {
    e.preventDefault();
    let prepay = [];
    let prepay_penalties = "";
    if (this.state.prepay_yrs) {
      this.state.prepay_yrs.forEach((propType, index) => {
        var obj = {};
        obj["prepay_yr_" + parseInt(index + 1, 10)] = propType;
        prepay.push(obj);
      });
    }
    if (prepay && prepay.length) {
      prepay_penalties = JSON.stringify(prepay);
    }

    this.props.missingQuotesActions.submitForm(
      this.props.previousPage,
      this.state.quote_id,
      this.state.quoted_amount
        ? this.state.quoted_amount.toString().replace(/,/g, "")
        : "",
      this.state.rate_type,
      this.state.spread_type,
      this.state.spread,
      this.state.manual_index_rate,
      this.state.lock_rate,
      this.state.rate_floor,
      this.state.quote_index,
      this.state.rate,
      this.state.term,
      this.state.amortization,
      this.state.ltv,
      this.state.prepay_type,
      this.state.recourse,
      this.state.bank_fee ? parseInt(this.state.bank_fee, 10) : "",
      prepay_penalties,
      this.state.number_of_option,
      this.state.option,
      this.state.new_quote_notes,
      this.state.index_datetime,
      this.state.term_sheet,
      this.state.is_new
    );
    this.setState({
      cancelButtonShowing: false,
    });
    if (this.props.previousPage === "MQ_BROKER") {
      this.props.closeForm();
    }
  }

  render() {
    return (
      <Panel className="quote-panel">
        <div>
          <Row>
            <Col md="6">
              <p className="quote-bank-name">{this.state.bank_name}</p>
              {/*<p className="quote-deal-name">{this.state.deal_name}</p>*/}
            </Col>
            {this.props.previousPage === "MQ_BROKER" ? (
              <Col md="6">
                <i
                  className="material-icons close-quote"
                  onClick={this.props.closeForm}
                >
                  close
                </i>
              </Col>
            ) : (
              ""
            )}
          </Row>
          <Form>
            <Row>
              <Col md="5" sm="6" xs="8" className="quote-col-left">
                <Input
                  label="Banker"
                  floatingLabel={true}
                  className="no-border"
                  disabled={true}
                  value={this.state.banker}
                  onChange={() => ({})}
                />
              </Col>

              <Col md="5" sm="6" xs="4" className="quote-col-right">
                <Input
                  label="Requested On"
                  floatingLabel={true}
                  className="no-border"
                  disabled={true}
                  value={
                    moment(this.state.req_datetime).isValid()
                      ? moment(new Date(this.state.req_datetime)).format(
                          "MM.DD.YYYY"
                        )
                      : "--"
                  }
                  onChange={() => ({})}
                />
              </Col>
            </Row>
            <Row>
              <Col md="2" sm="6" className="quote-col-left">
                <CurrencyInput
                  label="Amount Quoted"
                  value={this.state.quoted_amount}
                  className="dollar-input"
                  update={this.quotedAmountChange.bind(this)}
                  floatingLabel={true}
                />
              </Col>

              <Col md="2" sm="6" className="quote-col-left">
                <Select
                  value={this.state.rate_type}
                  onChange={this.rateTypeChange}
                  label="Rate Type"
                >
                  <Option value="Fixed" label="Fixed" />
                  <Option value="Spread" label="Spread" />
                </Select>
              </Col>

              {this.state.rate_type === "Spread" ? (
                <div>
                  <Col md="2" sm="6" className="quote-col-left">
                    <Select
                      value={this.state.spread_type}
                      onChange={this.spreadTypeChange.bind(this)}
                      label="Spread Type"
                    >
                      <Option value="fix" label="Fixed Spread" />
                      <Option value="float" label="Floater Spread" />
                    </Select>
                  </Col>

                  <Col md="2" sm="6" className="quote-col-left">
                    <PercentageInput
                      label="Spread"
                      value={this.state.spread}
                      floatingLabel={true}
                      onBlur={this.spreadBlur.bind(this)}
                      update={this.spreadChange.bind(this)}
                    />
                  </Col>

                  <Col md="2" sm="6" className="quote-col-left">
                    <Select
                      label="Index"
                      className="index-select"
                      value={this.state.index}
                      onChange={this.changeIndex}
                    >
                      {Object.keys(this.props.missingQuotes.rateSpreadInt).map(
                        (key, index) => {
                          return (
                            <Option
                              value={key.toLowerCase()}
                              key={index}
                              label={`${key}`}
                            />
                          );
                        }
                      )}
                    </Select>
                  </Col>

                  <Col md="2" sm="6" className="quote-col-left">
                    <PercentageInput
                      label="Index Rate"
                      value={
                        this.state.manual_index_rate ||
                        this.state.index_today_rate
                      }
                      floatingLabel={true}
                      decimalPlaces={3}
                      update={this.indexRateChange.bind(this)}
                    />
                  </Col>

                  <Col md="2" sm="6" className="quote-col-left">
                    <PercentageInput
                      label="Rate"
                      disabled={true}
                      value={this.state.rate}
                      floatingLabel={true}
                      decimalPlaces={3}
                      update={this.rateChange.bind(this)}
                    />
                  </Col>

                  {this.state.spread_type === "fix" ? (
                    <Col md="2" sm="6" className="quote-col-left flex">
                      <div className="rkmd-checkbox checkbox-ripple flex-center">
                        <label className="input-checkbox checkbox-lightBlue">
                          <input
                            type="checkbox"
                            id="checkbox-1"
                            checked={this.state.lock_rate}
                            onChange={() =>
                              this.setState({
                                lock_rate: !this.state.lock_rate,
                              })
                            }
                          />
                          <span className="checkbox"></span>
                          <span className="ripple"></span>
                        </label>
                        <p htmlFor="checkbox-1" className="label">
                          Lock Rate
                        </p>
                      </div>
                    </Col>
                  ) : (
                    <Col md="2" sm="6" className="quote-col-left">
                      <PercentageInput
                        label="Rate Floor"
                        value={this.state.rate_floor}
                        floatingLabel={true}
                        update={this.rateFloorChange.bind(this)}
                      />
                    </Col>
                  )}
                </div>
              ) : (
                <Col md="2" sm="6" className="quote-col-left">
                  <PercentageInput
                    label="Rate"
                    value={this.state.rate}
                    decimalPlaces={3}
                    floatingLabel={true}
                    update={this.rateChange.bind(this)}
                  />
                </Col>
              )}

              <Col md="2" sm="6" className="quote-col-left">
                <Input
                  label="Term"
                  floatingLabel={true}
                  value={this.state.term}
                  onBlur={this.termBlur}
                  onChange={this.termChange}
                />
              </Col>

              <Col md="2" sm="6" className="quote-col-left">
                <Input
                  label="Amortization"
                  floatingLabel={true}
                  value={this.state.amortization}
                  onChange={this.amortizationChange}
                />
              </Col>

              <Col md="2" sm="6" className="quote-col-left">
                <Input
                  label="LTV"
                  floatingLabel={true}
                  value={this.state.ltv}
                  onChange={this.ltvChange}
                />
              </Col>

              <Col md="2" sm="6" className="quote-col-left">
                <Input
                  label="Bank Fee (% or $ amount)"
                  floatingLabel={true}
                  value={this.state.bank_fee}
                  onChange={this.bankFeeChange}
                />
              </Col>

              <Col md="2" sm="6" className="quote-col-left">
                <Select
                  value={this.state.term_sheet}
                  onChange={this.termSheetChange}
                  label="Term Sheet"
                >
                  <Option value="Not Selected" label="Not Selected" />
                  <Option
                    value="Awaiting Term Sheet"
                    label="Awaiting Term Sheet"
                  />
                  <Option value="Got Term Sheet" label="Got Term Sheet" />
                </Select>
              </Col>

              <Col md="2" sm="6" className="quote-col-left">
                <Select
                  value={this.state.prepay_type}
                  onChange={this.prepayChange}
                  label="Prepay"
                >
                  <Option value="Step Down" label="Step Down" />
                  <Option value="Yield Maintenance" label="Yield Maintenance" />
                  <Option value="Defeasance" label="Defeasance" />
                  <Option value="Net Loss" label="Net Loss" />
                  <Option value="Swab Breakage" label="Swab Breakage" />
                  <Option value="No Prepay Penalty" label="No Prepay Penalty" />
                </Select>
              </Col>

              <Col md="2" sm="6" className="quote-col-left">
                <Select
                  value={this.state.recourse}
                  onChange={this.recourseChange}
                  label="Recourse"
                >
                  <Option value="Not Specified" label="Not Specified" />
                  <Option value="Non Recourse" label="Non Recourse" />
                  <Option value="Full Recourse" label="Full Recourse" />
                  <Option value="Partial Recourse" label="Partial Recourse" />
                  <Option value="Burn off Recourse" label="Burn off Recourse" />
                </Select>
              </Col>

              <Col md="2" sm="6" className="quote-col-left">
                <Input
                  label="Number of Options"
                  floatingLabel={true}
                  value={this.state.number_of_option}
                  onChange={this.numberOfOptions.bind(this)}
                />
              </Col>

              <Col md="2" sm="6" className="quote-col-left">
                <Input
                  label="Years per Option"
                  floatingLabel={true}
                  value={this.state.option}
                  onChange={this.yearsPerOption.bind(this)}
                />
              </Col>
            </Row>
            <Row>
              {this.state.prepay_yrs.length > 0 && (
                <p className="quote-orange-header">Prepay Penalties</p>
              )}
            </Row>
            <Row>
              {this.state.prepay_yrs.length > 0 &&
                this.state.prepay_yrs.map((yr, index) => {
                  var localIndex = index + 1;
                  return (
                    <Col
                      md="1"
                      sm="3"
                      xs="6"
                      key={index}
                      className="quote-col-one"
                    >
                      <Input
                        floatingLabel={true}
                        value={yr}
                        label={"Year " + localIndex}
                        onChange={this.prepayYearsChange.bind(this, index)}
                      />
                    </Col>
                  );
                })}
            </Row>
          </Form>
          {this.state.cancelButtonShowing ? (
            <Button
              variant="flat"
              className="save-quote-btn mui--z1"
              onClick={this.submitForm.bind(this)}
            >
              Save Quote
            </Button>
          ) : (
            <Button
              variant="flat"
              className="save-quote-btn mui--z1"
              disabled={true}
            >
              Save Quote
            </Button>
          )}

          {this.state.cancelButtonShowing ? (
            <Button
              variant="flat"
              className="add-quote-btn mui--z1"
              onClick={this.props.closeQuoteForm}
            >
              Cancel
            </Button>
          ) : (
            ""
          )}
        </div>
        <QuoteNotes
          quote_id={this.state.quote_id}
          quote_notes={this.state.quote_notes}
          new_quote_notes={this.state.new_quote_notes}
          submitNoteModal={this.submitNoteModal}
        />
      </Panel>
    );
  }
}

function mapStateToProps(state) {
  return {
    title: state.navTitle,
    missingQuotes: state.missingQuotes,
    comps: state.comps,
  };
}

function mapDispatchToProps(dispatch) {
  return {
    navActions: bindActionCreators(navigationTitleActions, dispatch),
    missingQuotesActions: bindActionCreators(missingQuotesActions, dispatch),
  };
}

export default connect(mapStateToProps, mapDispatchToProps)(QuotePage);
